
a{
    text-decoration: none;
    color:#222;
}
.list-item {
    position: relative;
    margin: 5px 0;
    height: 250px;
    img{
        width: 100%;
        height: 100%;
        object-fit: fill;
    }

    .mask{
        position: absolute;
        width: 100%;
        height: 80px;
        background: rgba(255,255,255,0.8);
        left:0;
        right:0;
        bottom:0px;
        display: flex;
        .left{
            text-align: center;
            padding: 10px;
            flex: 1;
            overflow: hidden;

            p{
                padding: 5px 10px;
                font-size: 12px;
                width: 185px;
            }
        }

        .right{
            text-align: center;
            padding: 10px;
            flex: 1;
            p,div{
                padding: 5px 10px;
                font-size: 12px;
            }
            .btn{
                border-radius: 5px;
                background: rgba(255,255,255,0.8);
                color:#84a0ae
            }
            h3{
                display: inline;
            }
        }
    }
}

