#search-header{
    background-color: #ff5555;
    color: #fff;
    padding: 10px;
    text-align: center;
    position: relative;

    .input-container {
        background-color: #fff;
        border-radius: 15px;
        
        padding: 5px;
        text-align: left;
        margin-left: 30px;
        margin-right:10px;

        i{
            color:#ccc;
        }

        input{
            border: none;
            overflow: hidden;
        }
    }

    .back-icon{
        width: 16px;
        height: 16px;
        margin-top: 6px;
    }
}